.header {
  & {
    padding: 30px 0px;
    margin: 0 10px;
  }

  &__inner {
    & {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }

    &-logo {
      color: #fff;
      font-size: 30px;
      text-transform: uppercase;
    }

    &-btn {
      padding: 10px 15px;
      font-weight: bold;
    }
  }

  @media (max-width: 735px) {
    .header__inner {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
    }
  }

  a {
    text-decoration: none;
  }

  .header-wrap {
    & {
      div {
        margin-left: 20px;
      }
    }
  }
}
